.calling {
  position: relative;
  top: -10px;
  margin-left: 26px;

  &:after {
    position: absolute;
    content: '';
    bottom: 0;
    left: 0;
    width: 100%;
    height: 1px;
    background: rgba(208, 51, 77, 0.3);
  }
}

